@import "admin";
@import "post";
@import "postDetail";
@import "category";
@import "car";

.comment-action {
  padding-left: 8px;
  cursor: "auto";
}

[class*="-col-rtl"] .comment-action {
  padding-right: 8px;
  padding-left: 0;
}
.slick-track {
  height: 0px !important;
}
input[type="textt"] {
  background-color: #012e4f;
  color: white;
  border: none;
  height: 28px;
}

@font-face {
  font-family: "Museo_Slab_500_2";
  src: url("./Museo_Slab_500_2-webfont.woff2") format("woff2"),
    url("./Museo_Slab_500_2-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "HelveticaNeue-Regular";
  src: url("./HelveticaNeue-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Museo_Slab_500_2;
  font-style: normal;
  // font-weight: 100;
}

@font-face {
  font-family: "Objective-Bold";
  src: url("./Objective-Bold.woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Objective-Light";
  src: url("./Objective-Light.woff2");
  font-weight: normal;
  font-style: normal;
}
