  flex-direction: column;
}

.post-link {
  text-decoration: none;
}

.comps {
  width: 275px;

  ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin-bottom: 0px;
    .Reading-time {
      margin-right: 25%;
