.post-thumbnail {
  margin-top: 2%;
  margin-left: 2%;
  margin-bottom: 15px;
  height: 100px;
  width: 100px;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
}
.post-thumbnail-runner-one {
  margin-bottom: 15px;
  height: 320px;
  width: 100%;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
  border-radius: 6px;
}
.post-thumbnail-runner-two {
  margin-bottom: 15px;
  height: 380px;
  width: 100%;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
  border-radius: 6px;
}
.post-thumbnail-runner-three {
  margin-bottom: 15px;
  height: 100px;
  width: 320px;
  object-fit: cover;
  object-fit: cover !important;
  object-position: 50% 50% !important;
  padding-right: 0px;
  padding-left: 0px;
  border-radius: 6px;
}

