.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: transparent;
}

.Container:hover .overlay {
  opacity: 1;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 90%;
  left: 80%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  text-shadow: 6px 6px 8px #000000;
  text-decoration-style: solid;
}

.text-left {
  font-size: 14px;
  font-weight: bolder;
  color: white;
  font-size: 14px;
  position: absolute;
  top: 90%;
  right: 100px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  text-shadow: 6px 6px 8px #000000;
  text-decoration-style: solid;
  display: flex;
  flex-direction: row;
}
